#Works {
    padding: 100px 30px;
    background-color: dimgrey;
    display: flex;
    align-items: center;
    justify-content: center;
}

.Works__wrapper {
    max-width: 1300px;
    min-height: 50vh;
    margin: auto;
    padding: 0 40px;
}

.Works__wrapper h1 {
    color: #ffffff;
    text-align: center;
    margin-bottom: 80px;
    position: relative;
    font-size: 26px;
    padding-bottom: 20px;
}

.Works__wrapper h1::before {
    content: "";
    position: absolute;
    width: 80px;
    height: 3px;
    background-color: #111111;
    bottom: 0;
    left: calc(50% - 40px);
}

.Works__wrapper h1::after {
    content: "";
    position: absolute;
    width: 16px;
    height: 10px;
    background-color: #111111;
    border: 4px solid dimgrey;
    left: calc(50% - 12px);
    bottom:  -7px;
}

.Works__posts {
    width: min(1200px, 100%);
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    cursor: pointer;
}

.Works__post {
    width: calc(33% - 10px);
    overflow: hidden;
}

.Works__post__img {
    width: 100%;
    border-radius: 6px;
    transition: .3s linear;
}

.Works__post__content {
    background-color: #ffffffdd;
    margin: 0 20px;
    padding: 30px;
    border-radius: 6px;
    transform: translateY(-60px);
    transition: .3s linear;
}

.Works__post__content h3 {
    font-size: 20px;
    margin-bottom: 10px;
    color: #111111
}

.Works__post__content h4 {
    font-size: 16px;
    color: #111111
}

.Works__date {
    font-size: 15px;
    font-style: italic;
    color: #e77f67;
}

.Works__post:hover .Works__post__img {
    transform: translateY(20px);
}

.Works__post:hover .Works__post__content  {
    transform: translateY(-80px);
}

@media  screen and (max-width: 1200px){
    .Works__posts{
        justify-content: center;
    }

    .Works__post {
        width: min(600px, 100%);
    }
}

@media  screen and (max-width: 600px){

    .Works__post__content {
        padding: 10px;
    }
    
    .Works__post__content h3 {
        font-size: 16px;
    }
    
    .Works__post__content h4 {
        font-size: 14px;
    }
    
    .Works__date {
        font-size: 12px;
    }
}


