.React::after{
    width: 75%;
    -webkit-animation: React 3s;
            animation: React 3s;
}

.JavaScript::after{
    width: 70%;
    -webkit-animation: JavaScript 3s;
            animation: JavaScript 3s;
}

.HTML::after{
    width: 90%;
    -webkit-animation: HTML 3s;
            animation: HTML 3s;

}

.CSS::after{
    width: 80%;
    -webkit-animation: CSS 3s;
            animation: CSS 3s;
}

@-webkit-keyframes React {
    0%{
        width: 0%;
    }
    100%{
        width: 75%;
    }
}

@keyframes React {
    0%{
        width: 0%;
    }
    100%{
        width: 75%;
    }
}

@-webkit-keyframes JavaScript {
    0%{
        width: 0%;
    }
    100%{
        width: 70%;
    }
}

@keyframes JavaScript {
    0%{
        width: 0%;
    }
    100%{
        width: 70%;
    }
}

@-webkit-keyframes HTML {
    0%{
        width: 0%;
    }
    100%{
        width: 90%;
    }
}

@keyframes HTML {
    0%{
        width: 0%;
    }
    100%{
        width: 90%;
    }
}

@-webkit-keyframes CSS {
